.header-categori{
  margin: 0;
  padding: 0;
  list-style-type: none;
  /* background-color: bisque; */
  overflow: auto;
  /* flex-direction: row; */
  white-space: nowrap;
  -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
}
.header-categori::-webkit-scrollbar {
  display: none; /* Chrome, Safari, Opera*/
}


.header-categori > li{
  display: inline-block;
  padding: 20px;
  width: 50px;

}
.header-categori > li > a {
  text-decoration: none;
  color: black;
  
}

.header-categori > li > * {
  display: flex;
  /* background-color: aqua; */
  text-decoration: none; 
  justify-content: center;
}


.card {
	/* height: 100px; */
	/* width: 90%; */
	border-radius: 5px;
	display: inline-block;
  flex-direction: row;
	margin-top: 10px;
	/* margin-left: 30px; */
	position: relative;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1), 0 6px 20px 0 rgba(0, 0, 0, 0.1);
	overflow: hidden;
  display: flex;
  flex-direction: row;
}

.card > div:nth-child(1) {
  display: flex;
  flex: 1;
  display: inline-block;
  /* background-color: rgb(234, 192, 246); */
  text-align: center;
}

.card > div:nth-child(2) {
  display: flex;
  flex: 2.5;
  /* background-color: rgb(164, 244, 220); */
  flex-direction: column;
}
.active{
  /* background-color: cyan; */
  border: 1px;
  color: darkorange;
  border: solid;
  border-bottom-width: 4px;
  border-top-width: 0px;
  border-left-width: 0px;
  border-right-width: 0px;
}


/* 
 auth button
*/
.auth-button{
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  
  background:#23dbcb;
  color: white;
  
  margin: 0;
  padding: 0.5rem 1rem;

  font-size: 1rem;
  font-weight: 400;
  text-align: center;
  text-decoration: none;
  
  border: none;
  border-radius: 4px;
  
  display: inline-block;
  width: auto;
  
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  
  cursor: pointer;
  font-weight: bold;
  transition: 0.5s;
  width: 90%;
}
